import React, { RefObject, useRef } from 'react';
import {
    mergeProps,
    useFocusRing,
    useSliderThumb,
    VisuallyHidden,
} from 'react-aria';
import { SliderState } from 'react-stately';
import classNames from 'classnames';

import SvgEnefit from '../Icons/Enefit';

import styles from './InputSlider.module.scss';

interface SliderThumbProps {
    state: SliderState;
    trackRef: RefObject<Element>;
    index: number;
}

const Thumb = (props: SliderThumbProps) => {
    const inputRef = useRef(null);
    const { state, trackRef, index } = props;
    const { thumbProps, inputProps, isDragging } = useSliderThumb(
        {
            index,
            trackRef,
            inputRef,
        },
        state
    );
    const { focusProps, isFocusVisible } = useFocusRing();

    const classList = classNames(styles.slider__thumb, {
        [styles[`slider__thumb--dragging`]]: isDragging,
        [styles[`focus-ring`]]: isFocusVisible,
    });

    return (
        <div {...thumbProps} className={classList}>
            <VisuallyHidden>
                <input ref={inputRef} {...mergeProps(inputProps, focusProps)} />
            </VisuallyHidden>
            <SvgEnefit aria-hidden="true" />
        </div>
    );
};

export default Thumb;
