import React, { useRef } from 'react';
import { AriaSliderProps, useNumberFormatter, useSlider } from 'react-aria';
import { useSliderState } from 'react-stately';
import classNames from 'classnames';

import Thumb from './Thumb';

import styles from './InputSlider.module.scss';

export interface InputSliderProps extends AriaSliderProps {
    className?: string;
    formatOptions?: Intl.NumberFormatOptions;
    showOutput?: boolean;
}

const InputSlider = (props: InputSliderProps) => {
    const trackRef = useRef(null);
    const { label, formatOptions, className, showOutput } = props;
    const numberFormatter = useNumberFormatter(formatOptions);
    const state = useSliderState({ ...props, numberFormatter });
    const { isDisabled, getThumbValueLabel, getThumbPercent } = state;
    const valuePercent = getThumbPercent(0) * 100;
    const { groupProps, trackProps, labelProps, outputProps } = useSlider(
        props,
        state,
        trackRef
    );

    const classList = classNames(styles.slider, className, {
        [styles[`slider--disabled`]]: isDisabled,
    });

    return (
        <div {...groupProps} className={classList}>
            {(label || showOutput) && (
                <div className={styles['slider__label-container']}>
                    <div className={styles['slider__label-wrapper']}>
                        {label && (
                            <label
                                {...labelProps}
                                className={styles.slider__label}
                            >
                                {label}
                            </label>
                        )}
                    </div>
                    {showOutput && (
                        <output
                            {...outputProps}
                            className={styles.slider__output}
                        >
                            {getThumbValueLabel(0)}
                        </output>
                    )}
                </div>
            )}
            <div
                {...trackProps}
                ref={trackRef}
                className={styles.slider__track}
            >
                {!isDisabled && (
                    <span
                        className={styles['slider__track--highlight']}
                        style={{ width: `${valuePercent}%` }}
                    ></span>
                )}
                <Thumb index={0} state={state} trackRef={trackRef} />
            </div>
        </div>
    );
};

export default InputSlider;
